/** @jsx jsx */
import { fireEvent, jsx, renderEditor, waitForTime } from '@ali/4ever-dev-test';
import { wait, waitFor, waitForElementToBeRemoved } from '@testing-library/react';
import BiEditor from '../editor';
import { symbolSelector } from './constants';
import { ToolbarLayout } from '@ali/we-design-next';

let origin_value
beforeAll(() => {
  origin_value = process.env.RUNTIME_ENV;
  process.env.RUNTIME_ENV = ToolbarLayout.UITEST_FLAG
})

afterAll(() => {
  process.env.RUNTIME_ENV = origin_value;
})


function clickToolbar(container, selector) {
  const quoteBlockElement = container.querySelector(selector);
  fireEvent.click(quoteBlockElement);
}

function selectSymbol(content) {
  const listSymbol = content.querySelector(symbolSelector);
  fireEvent.click(listSymbol);
}

describe('list', () => {
  describe('列表项目符号选区', () => {
    const value = (
      <value>
        <document>
          <anchor />
          <block
            type="paragraph"
            data={{
              list: {
                listId: 'dn7ncrq6m7j',
                level: 0,
                isOrdered: true,
                listStyleType: 'DEC_LEN_LROM_P',
              },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: {
                listId: 'dn7ncrq6m7j',
                level: 1,
                isOrdered: true,
                listStyleType: 'DEC_LEN_LROM_P',
              },
            }}
          >
            bbb
          </block>
          <block
            type="paragraph"
            data={{
              list: {
                listId: 'dn7ncrq6m7j',
                level: 2,
                isOrdered: true,
                listStyleType: 'DEC_LEN_LROM_P',
              },
            }}
          >
            ccc
          </block>
          <focus />
        </document>
      </value>
    );

    const selectedSymbolSelector = '.isSelected .list-symbol-overlay';
    it('点击项目符号，显示选中符号选区， 点击其他地方，隐藏符号选区', async () => {
      const { content } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );
      selectSymbol(content);
      await waitFor(() => {
        const selectedSymbol = content.querySelector(selectedSymbolSelector);
        expect(typeof (selectedSymbol)).toBe('object');
      });

      const listText = content.querySelector('[data-type="paragraph"]');
      fireEvent.click(listText);
      await waitFor(() => {
        const selectedSymbol = content.querySelector(selectedSymbolSelector);
        expect(selectedSymbol).toBe(null);
      });
    });

    // TODO：框架升级到3.0后，单测不通过，暂时skip
    // 颜色选择器首次打开的时间太长，直接测试会挂掉
    it('选中项目符号, 首次展开颜色选择器', async () => {
      const selector = 'bi-toolbar-highlight';
      const testId = 'overlay-bi-toolbar-highlight';
      const { container, queryByTestId } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );
      // 避免选中离屏渲染的 toolbar
      const offScreenToolbarItem = container.querySelector(`[class*="OffScreen"] [data-testid="${selector}"]`);
      if (offScreenToolbarItem) {
        await waitForElementToBeRemoved(offScreenToolbarItem);
      }
      await waitFor(() => queryByTestId(selector));
      clickToolbar(
        container,
        `[data-testid="${selector}"] .wdn-toolbar-split-btn-arrow`,
      );
    });

    [
      {
        name: '背景颜色',
        selector: 'bi-toolbar-highlight',
        testId: 'bi-toolbar-highlight-color-picker',
        styleName: 'backgroundColor',
      },
      {
        name: '符号颜色',
        selector: 'bi-toolbar-color',
        testId: 'bi-toolbar-color-color-picker',
        styleName: 'color',
      },
    ].forEach(({ name, selector, testId, styleName }) => {
      it(`选中项目符号，设置${name}`, async () => {
        const { container, content, queryByTestId, queryAllByTestId } = await renderEditor(
          <BiEditor defaultValue={value} />,
        );
        await waitFor(() => queryByTestId(selector));
        selectSymbol(content);
        await wait();
        clickToolbar(
          container,
          `[data-testid="${selector}"] .wdn-toolbar-split-btn-arrow`,
        );
        const colorPicker = await waitFor(() => queryByTestId(testId));
        clickToolbar(colorPicker, '[color="#FEFF00"]');
        await wait();
        const listSymbol = content.querySelector(symbolSelector);
        expect(listSymbol?.style[styleName]).toBe('rgb(254, 255, 0)');
      });
    });

    [
      {
        name: '加粗',
        selector: 'bold',
        styleName: 'fontWeight',
      },
      {
        name: '斜体',
        selector: 'italic',
        styleName: 'fontStyle',
      },
    ].forEach(({ name, selector, styleName }) => {
      it(`选中项目符号，一次点击${name}, 二次点击取消${name}`, async () => {
        const { container, content } = await renderEditor(
          <BiEditor defaultValue={value} />,
        );
        const toolbarBtn = `[data-testid="bi-toolbar-${selector}"]`;
        selectSymbol(content);
        clickToolbar(container, toolbarBtn);
        await waitFor(() => {
          const listSymbol = content.querySelector(symbolSelector);
          expect(listSymbol?.style[styleName]).toBe(selector);
        });
        clickToolbar(container, toolbarBtn);
        await waitFor(() => {
          const listSymbol = content.querySelector(symbolSelector);
          expect(!!listSymbol?.style[styleName]).toBe(false);
        });
      });
    });

    it('选中项目符号，先设置字体大小，再清除样式', async () => {
      const { container, content, queryByText } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );
      selectSymbol(content);
      clickToolbar(container, '[data-testid="bi-toolbar-sz"]');
      const fontBtn = queryByText('42');
      fireEvent.click(fontBtn);
      const listSymbol = await waitFor(() => {
        return content.querySelector(symbolSelector);
      });
      expect(listSymbol?.style.fontSize).toBe('42pt');
      clickToolbar(container, '[data-testid="bi-toolbar-clearstyle"]');
      await waitForTime(100);
      const listItems = await waitFor(() => {
        return content.querySelectorAll(symbolSelector).length;
      });
      // 清除格式将清除列表属性
      await waitFor(() => {
        expect(listItems).toBe(0);
      });
    });

    it('设置有序列表中部分列表项目为第三种有序列表类型后，选中项目符号，列表序号不会变化', async () => {
      const value = (
        <value>
          <document>
            <block
              type="paragraph"
              data={{
                list: {
                  listId: 'dn7ncrq6m7j',
                  level: 0,
                  isOrdered: true,
                  listStyleType: 'DEC_LEN_LROM_P',
                },
              }}
            >
              aaa
            </block>
            <block
              type="paragraph"
              data={{
                list: {
                  listId: 'dn7ncrq6m7j',
                  level: 0,
                  isOrdered: true,
                  listStyleType: 'DEC_LEN_LROM_P',
                },
              }}
            >
              bbb
            </block>
            <anchor />
            <block
              type="paragraph"
              data={{
                list: {
                  listId: 'mu7dthv6h7j',
                  level: 1,
                  isOrdered: true,
                  listStyleType: 'DEC_DEC_DEC_P',
                },
              }}
            >
              ccc
              </block>
            <focus />
          </document>
        </value>
      );
      const { content } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );
      const symbol = content.querySelectorAll(symbolSelector);
      expect(symbol[2].textContent).toBe('1.1.');
      fireEvent.click(symbol[2]);
      await wait();
      await waitFor(() => {
        const sym = content.querySelectorAll(symbolSelector);
        expect(sym[2].textContent).toBe('1.1.');
      });
    });
  });
});
